.container {
  height: 3rem;
  width: fit-content;
  margin-top: 1rem;
  margin-bottom: 1rem;
  padding: .2rem .5rem;
  display: flex;

  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: white;

  P {
    text-align: center;
    font: var(--textParaStandard);
    margin: 0 1rem;
    // min-width: 2rem;

    &:last-of-type {
      color: var(--colorAttention);
    }

    &:first-of-type {
      color: var(--colorSecondary); // padding: 0;
    }

  }

  svg {
    margin: 0 1rem;
    height: 2rem;
    width: 2rem;

    &:last-of-type {
      transform: rotate(180deg)
    }
  }

}